
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getBarNotice, saveBarNotice } from '@/api/'
export default {
components: {
Group,
XTextarea,
XButton
},
data () {
return {
content: '',
loading: false
}
},
beforeRouteEnter (to, from, next) {
document.title = '公告设置'
next()
},
created () {
getBarNotice({ht_id: this.$route.params.id}).then((res) => {
this.content = res.result ? res.result.content : ''
})
},
methods: {
saveNotice () {
if (this.content === '') {
this.$vux.toast.show({
text: '公告内容不能为空'
})
return
}
this.loading = true
saveBarNotice({ht_id: this.$route.params.id, content: this.content}).then((res) => {
this.$vux.toast.show({
text: '保存成功'
})
}).finally(() => {
this.loading = false
})
}
}
}



